import React, { useState , useEffect } from 'react';
import ReactDOM from 'react-dom';
import axios from 'axios';
import './custom.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import Button from 'react-bootstrap/button';
import Form from 'react-bootstrap/form';

/// DETERMINES WHERE AXIOS QUERIES INFO ///
const baseUrl = '/api'

const App = () => {
  const [newItem, setNewItem] = useState('')
  const [groceryList, setGroceryList] = useState([])

  const getGroceryList = async () => {
    const list = await axios.get(baseUrl)
    setGroceryList(groceryList.concat(list.data))
  }

  useEffect(() => getGroceryList(), [])

  const handleNewItem = (event) => {
    console.log(event.target.value)
    setNewItem(event.target.value)
  }

  const addCustomItem = async (event) => {
    event.preventDefault()
    const newGroceryItem = {
      item: newItem,
    }
    const response = await axios.post(baseUrl, newGroceryItem)
    newGroceryItem.id = response.data.id
    setGroceryList(groceryList.concat(newGroceryItem))
    setNewItem('')
  }

  const addCommonItem = async (event) => {
    console.log(event.target.innerText)
    const newGroceryItem = {
      item: event.target.innerText,
    }
    const response = await axios.post(baseUrl, newGroceryItem)
    newGroceryItem.id = response.data.id
    setGroceryList(groceryList.concat(newGroceryItem))
  }

  const deleteItem = async (id) => {    
    const updatedList = groceryList.filter(item => item.id !== id)
    setGroceryList(updatedList)
    await axios.delete(`${baseUrl}/${id}`)
  }

  return (
    <div className="container">
    <Form onSubmit={addCustomItem}>
      <Form.Group controlId="formAddItem">
        <Form.Label>Item</Form.Label>
        <Form.Control value={newItem} type="item" placeholder="Enter grocery item" onChange={handleNewItem} />
      </Form.Group>
      <Button variant="dark" size="lg" type="submit" block>
        Add Item
      </Button>
    </Form>
    <br />
    <Button variant="dark" size="lg" className="commonItems" onClick={addCommonItem} block>Bacon</Button>
    <Button variant="dark" size="lg" className="commonItems" onClick={addCommonItem} block>Eggs</Button>
    <Button variant="dark" size="lg" className="commonItems" onClick={addCommonItem} block>Cereal</Button>
    <Button variant="dark" size="lg" className="commonItems" onClick={addCommonItem} block>Greens</Button>
    <Button variant="dark" size="lg" className="commonItems" onClick={addCommonItem} block>Cheese</Button>
    <Button variant="dark" size="lg" className="commonItems" onClick={addCommonItem} block>Chips</Button>
    <Button variant="dark" size="lg" className="commonItems" onClick={addCommonItem} block>Coffee</Button>
    <Button variant="dark" size="lg" className="commonItems" onClick={addCommonItem} block>Tea</Button>
    <Button variant="dark" size="lg" className="commonItems" onClick={addCommonItem} block>Cream Cheese</Button>
    <Button variant="dark" size="lg" className="commonItems" onClick={addCommonItem} block>Deli Meats</Button>
    <Button variant="dark" size="lg" className="commonItems" onClick={addCommonItem} block>Cured Meats</Button>
    <Button variant="dark" size="lg" className="commonItems" onClick={addCommonItem} block>Bread</Button>
    <Button variant="dark" size="lg" className="commonItems" onClick={addCommonItem} block>Bagels</Button>

    <h1 className="margin-fix">Grocery List</h1>
    {groceryList.map(item => {
      return <h5 key={item.id}>{item.item} <Button variant="dark" size="lg" onClick={() => deleteItem(item.id)}>Delete</Button></h5>
    })}
    </div>
  )
}

ReactDOM.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>,
  document.getElementById('root')
);


